
import Search from 'vux/src/components/search/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import {mapState,mapGetters, mapActions} from 'vuex'
import { cloneObj ,sessionUtil} from "@/utils/utils.js";
export default {

data(){
return{
search:'',
disabled:false,
newsData:[],
pageNum:0,
pageSize:10,
isEnd:false,
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
pulldown: {
content: '',
height: 60,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup: {
content: '',
pullUpHeight: 50,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pullup-'
},
sqlTableName:'U008_Graphic_Progress'

}
},
components:{
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Scroller,
Spinner
},
computed:{
...mapState({
userUid: state => state.userUid

})
},
created(){


},
mounted(){

},
activated(){
var type = this.util.getUrlParam("pType");
this.refresh ();

this.$refs.scroller.reset();

}
,
methods:{
searchFunc(){
this.refresh()
},
showDetail(index){
var _this = this

this.$router.push({name:'noteDetails',query:{Id:_this.newsData[index].Id}})
},

loadMore () {
console.log('More--pageNum',this.pageNum)
this.querryData(1,this.pageNum)


setTimeout(() => {
this.$refs.scroller.donePullup();

}, 10)

},
refresh () {
this.pageNum=0;

this.querryData(0,this.pageNum)
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup();
}, 0);

})

},
querryData(type,start){
this.util.mask()

var  _this = this;

var url = "/admin/appAgentAction!runAgent.action?agentId=Agent_SCHD_Index_Data_Leader&appId=SCHD&operateType=getMailbox2";
var params = {start:_this.pageNum,limit:_this.pageSize}
this.util.get(url,params,function(res){
console.log(res)
_this.newsData=type ==1? _this.newsData.concat(res.data):cloneObj(res.data);
if( res.data.length < _this.pageSize ){
_this.changePullupStatus(false);
_this.isEnd = true;
}
_this.pageNum+=res.data.length;
_this.util.unmask()
})
}
,
changePullupStatus (enabled) {
if (enabled) {
this.$refs.scroller.enablePullup()
this.pullupEnabled = true;
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false;

}
}

}
}



